@fontColor:#373d46;
@selectFontColor:#ffffff;
@themeColor:#047adf;
@themeBackground:#fafafa;
@thColor:#f5f5f5;
@trInterval:#f8fbff;
@trHover:#f9ffba;
@menuColor:#2f3a4c;
@headerHeight:80px;
@menuWidth:255px;
@menuItemHeight:50px;
@menuItemColor:#2d3846;
@menuItemSelect:#047adf;
@subItemHeight:55px;
@tabWidth:150px;

/*----------------------------
            框架样式
 -----------------------------*/
html{
  font-family:"Microsoft Yahei","Droid Sans Fallback","Arial","Helvetica","sans-serif","宋体";font-size:14px;outline: none;color: @fontColor;
  background-color: @themeBackground;
}

.frame3{
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  >header{
    height:@headerHeight;
    z-index: 1;
    user-select: none;
    &.full-screen{
      height:0;
    }
  }
  .frame-content{
    display: flex;
    flex:1 1 auto;
    height: 1px;
    z-index: 0;
  }
  >footer{
    height:0;
  }
}

.frame3-header{
  height:@headerHeight;
  background: @themeColor url(img/head-bg.png) no-repeat 100% 130%;
  display: flex;
  position: relative;
  z-index: 9999;
  -webkit-transition: all 300ms;
  -moz-transition: all 300ms;
  -ms-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;
  &.full-screen{
    height: 0;
    *{
      display:none;
    }
  }
  .system-info{
    color: @selectFontColor;
    display: flex;
    position: relative;
    z-index: 9999;
    background: #1382ea;
    padding: 0 100px 0 0;
    .logo img{
      height:100%;
      width: 80px;
    }
    .system-name{
      padding: 16px 0 0 10px;
      >div:first-child{
        font-size: 20px;
        font-weight: bold;
      }
    }
  }
  .main-menu{
    flex: 1;
    width: 100px;
    .tab-wrapper{
      height: 100%;
      .tab-tool{
        background: #1382ea;
        &:hover{
          background-color: #0251ae;
        }
        i{
          line-height: 80px;
        }
      }
      .tab-tabs{
        &>ul{
          height:100%;
          li{
            flex-direction: column;
            background: rgb(37,135,236);
            padding: 10px;
            height: 100%;
            margin: 0;
            border-right: solid 1px rgb(4,109,218);
            &>i{
              font-size: 30px;
            }
            .title{
              color: rgb(208,222,248);
            }
            .nav-icon{
              width: 30px;
              margin: auto;
            }
          }
          li:hover,li.active{
            background: rgb(177,215,254);
            &>i{
              color: rgb(4,92,189);
            }
            .title{
              color: rgb(4,92,189);
            }
          }
        }
      }
    }
  }
  .user-info{
    width: 230px;
    color: white;
    position: relative;
    z-index: 9999;
    background: #0e6fea;
    padding: 0 0 0 50px;
    >.btn-group{
      display: flex;
      .user-img{
        height: 60px;
        width: 60px;
        background: aliceblue;
        border-radius: 30px;
        margin-top: 10px;
        >div{
          height: 54px;
          width: 54px;
          background: red;
          border-radius: 30px;
          margin: 3px;
        }
      }
      .dropdown-toggle{
        line-height: 80px;
        margin-left: 10px;
        flex: 1;
        padding: 0 10px;
        cursor: pointer;
        height: 80px;
        >span:first-child{
          max-width: 65px;
          display: inline-block;
          line-height: 80px;
        }
        >span:last-child{
          font-size: 12px;
          padding-left: 5px;
          display: inline-block;
          position: absolute;
          top: 33px;
        }
      }
    }
  }
}
.frame-content{
  &>.menu{
    height:100%;
    width:60px;
    background: rgb(47,58,76);
    >nav{
      height:100%;
      .menu-item{
        height: 75px;
        color: white;
        text-align: center;
        padding: 10px 0;
        cursor: pointer;
        position: relative;
        i{
          font-size: 30px;
        }
        .title{
          width: 60px;
          display: inline-block;
          text-overflow: clip;
          color: #d1d1d3;
          margin-top: 5px;
        }
        .menu-arrow{
          margin: -42px 10px 0 50px;
          border-top: 8px solid transparent;
          border-bottom: 8px solid transparent;
          border-right: 13px solid white;
          position: absolute;
          z-index: 9999;
          display: none;
        }
      }
      .menu-item:hover{
        background: rgb(28,36,49);
      }
      .menu-item.active{
        background: rgb(28,36,49);
        .menu-arrow{
          display: block;
        }
      }
    }
  }
  &>.content{
    height:100%;
    flex:1;
    .map-content{
      position: relative;
      .map-content-left{
        position: absolute;
        height:100%;
        width: 280px;;
        background: white;
        z-index: 9999;
        display: none;
        .map-content-left.show{
          display: block;
        }
        &>div{
          height: 100%;
          &>div{
            height: 100%;
            overflow-y: auto;
            overflow-x: hidden;
          }
        }
      }
      .map-content-right{
        height:100%;
      }
    }
  }
}
/*----------------------------
            常用页面样式
 -----------------------------*/
.under-design{
  height: 100%;
  background: #a6c3de;
  text-align: center;
  display: flex;
  .page-content{
    margin: auto;
  }
  i{
    font-size: 120px;
    color: rgba(0,0,0,0.2);
    align-self: center;
  }
  .title{
    color: #FFF;
    font-size: 60px;
    text-align: center;
  }
}


/*----------------------------
            通用样式
 -----------------------------*/
.btn{
  border-radius: 0;
  min-width: 90px;
}
.ui-grid-top-panel{
  background: @thColor !important;
}

/*----------------------------
            插件补充样式
 -----------------------------*/
#loading-bar .bar {
  top: auto !important;
  left: auto !important;
  background-color: red;
}
#loading-bar-spinner {
  //background-color: red;
  color:red;
  top: auto !important;
  left: auto !important;
}
.fr-box.fr-basic .fr-element {
  white-space: normal;
  word-break: break-all;
}

/*----------------------------
            兼容低分屏样式
 -----------------------------*/
@media screen and (min-width: 1366px) and (max-width: 1600px){
  .query-form>header>h4{
    padding: 10px;
  }
  .ngdialog.ngdialog-theme-default{
    padding-top: 80px;
    padding-bottom: 80px;
  }
}
.pageload-overlay{
  position:absolute;
}
.pageload-overlay::after, .pageload-overlay::before{
  position:absolute;
}


@media screen and (max-width: 1366px) {
  .frame2 > header{
    height:114px !important;
  }
  .frame2 > header.full-screen {
    height: 44px !important;
  }
  .header-up{
    background: #047adf url(img/head-bg.png) no-repeat 100% !important;
    height:70px !important;
    .system-info .logo img{
      width:70px !important;
    }
    .tools .theme-option > span{
      line-height: 70px !important;
    }
    .user-info > .btn-group {
      .user-img{
        margin-top:5px !important;
      }
      .dropdown-toggle{
        line-height: 70px !important;
        height: 70px !important;
      }
    }

  }
  .header-up.full-screen {
    height: 0 !important;
  }
  .service-header{
    div:nth-child(1){
      width: 200px !important;
    }
  }
  .service-center{
    .service-tree{
      width:200px !important;
    }
  }

  .frame-content .menu .menu-item > {
    .nav{
      height:44px !important;
      padding: 12px 0px 0px 30px !important;
    }
    .sub-item > li{
      height:44px !important;
      padding-top:12px !important;
    }
  }
  .query-form {
    header h4{
      padding:10px 0px 10px 10px !important;
    }
    .query-content{
      padding: 0px 15px !important;
      .tool-bar{
        height:44px !important;
      }
      .query-condition{
        padding:8px 0px 0px 0px!important;
        .query-input{
          width:560px !important;
          .query-item{
            width: 280px !important;
            >div{
              width:180px !important;
            }
          }
        }
        .query-tool{
          padding-left:40px !important;
        }
      }
      .tool-bar .tool-tag .tag-container{
        margin:auto !important;
      }
      .tool-buttons{
        .btn-link{
          font-size: 14px !important;
          border: none !important;
        }
      }
      .content-table{
        table> tbody > tr:nth-child(even) > td, table> tbody > tr:nth-child(even) > th {
          background-color: #f9f9f9;
        }
        .table-striped > tbody > tr:nth-child(odd) > td, .table-striped > tbody > tr:nth-child(odd) > th{
          background-color: #ffffff !important;
        }
      }
      .app-content ul li > span > div > div {
        > div.title{
          height:25px !important;
          line-height: 25px !important;
        }
        > div.image{
          width:40% !important;
          padding-top:40% !important;
        }
        >div.image:hover{
          width: 45% !important;
          padding-top: 45% !important;
        }
      }
    }
  }
  .ngdialog.ngdialog-theme-default.custom.sm .ngdialog-content{
    width: 570px !important;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content {
    width: 900px !important;
    height:450px !important;
    .dialog-header{
      padding:10px !important;
    }
    .dialog-content{
      height:300px !important;
      padding:0px 30px 10px 30px!important;
    }
    .nav-tabs{
      margin:0px 10px;
    }
    .gm-pagination{
      .pager-loose{
        margin:10px 0px !important;
        line-height: 1em;
        > li > a{
          padding:3px 9px !important;
        }
      }
      .page-tool{
        margin: 13px 0 0 10px !important;
      }
    }
    .form-simple {
      .table{
        td.td-label{
          padding: 13px 8px 0px 8px !important;
          label{
            margin-bottom: 0px !important;
          }
        }
        td.td-value{
          padding:13px 50px 0px 8px !important;
          .form-control{
            height:25px !important;
            line-height: 1 !important;
          }
          .gm-tree-div{
            min-height:50px !important;
            .tree-a{
              font-size: 13px !important;
            }
          }
        }
      }
      tr.catagory span{
        margin-top:10px !important;
      }
    }
  }

}


