/*----------------------------
            框架样式
 -----------------------------*/
html {
  font-family: "Microsoft Yahei", "Droid Sans Fallback", "Arial", "Helvetica", "sans-serif", "宋体";
  font-size: 14px;
  outline: none;
  color: #373d46;
  background-color: #fafafa;
}
.frame3 {
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.frame3 > header {
  height: 80px;
  z-index: 1;
  user-select: none;
}
.frame3 > header.full-screen {
  height: 0;
}
.frame3 .frame-content {
  display: flex;
  flex: 1 1 auto;
  height: 1px;
  z-index: 0;
}
.frame3 > footer {
  height: 0;
}
.frame3-header {
  height: 80px;
  background: #047adf url(img/head-bg.png) no-repeat 100% 130%;
  display: flex;
  position: relative;
  z-index: 9999;
  -webkit-transition: all 300ms;
  -moz-transition: all 300ms;
  -ms-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;
}
.frame3-header.full-screen {
  height: 0;
}
.frame3-header.full-screen * {
  display: none;
}
.frame3-header .system-info {
  color: #ffffff;
  display: flex;
  position: relative;
  z-index: 9999;
  background: #1382ea;
  padding: 0 100px 0 0;
}
.frame3-header .system-info .logo img {
  height: 100%;
  width: 80px;
}
.frame3-header .system-info .system-name {
  padding: 16px 0 0 10px;
}
.frame3-header .system-info .system-name > div:first-child {
  font-size: 20px;
  font-weight: bold;
}
.frame3-header .main-menu {
  flex: 1;
  width: 100px;
}
.frame3-header .main-menu .tab-wrapper {
  height: 100%;
}
.frame3-header .main-menu .tab-wrapper .tab-tool {
  background: #1382ea;
}
.frame3-header .main-menu .tab-wrapper .tab-tool:hover {
  background-color: #0251ae;
}
.frame3-header .main-menu .tab-wrapper .tab-tool i {
  line-height: 80px;
}
.frame3-header .main-menu .tab-wrapper .tab-tabs > ul {
  height: 100%;
}
.frame3-header .main-menu .tab-wrapper .tab-tabs > ul li {
  flex-direction: column;
  background: #2587ec;
  padding: 10px;
  height: 100%;
  margin: 0;
  border-right: solid 1px #046dda;
}
.frame3-header .main-menu .tab-wrapper .tab-tabs > ul li > i {
  font-size: 30px;
}
.frame3-header .main-menu .tab-wrapper .tab-tabs > ul li .title {
  color: #d0def8;
}
.frame3-header .main-menu .tab-wrapper .tab-tabs > ul li .nav-icon {
  width: 30px;
  margin: auto;
}
.frame3-header .main-menu .tab-wrapper .tab-tabs > ul li:hover,
.frame3-header .main-menu .tab-wrapper .tab-tabs > ul li.active {
  background: #b1d7fe;
}
.frame3-header .main-menu .tab-wrapper .tab-tabs > ul li:hover > i,
.frame3-header .main-menu .tab-wrapper .tab-tabs > ul li.active > i {
  color: #045cbd;
}
.frame3-header .main-menu .tab-wrapper .tab-tabs > ul li:hover .title,
.frame3-header .main-menu .tab-wrapper .tab-tabs > ul li.active .title {
  color: #045cbd;
}
.frame3-header .user-info {
  width: 230px;
  color: white;
  position: relative;
  z-index: 9999;
  background: #0e6fea;
  padding: 0 0 0 50px;
}
.frame3-header .user-info > .btn-group {
  display: flex;
}
.frame3-header .user-info > .btn-group .user-img {
  height: 60px;
  width: 60px;
  background: aliceblue;
  border-radius: 30px;
  margin-top: 10px;
}
.frame3-header .user-info > .btn-group .user-img > div {
  height: 54px;
  width: 54px;
  background: red;
  border-radius: 30px;
  margin: 3px;
}
.frame3-header .user-info > .btn-group .dropdown-toggle {
  line-height: 80px;
  margin-left: 10px;
  flex: 1;
  padding: 0 10px;
  cursor: pointer;
  height: 80px;
}
.frame3-header .user-info > .btn-group .dropdown-toggle > span:first-child {
  max-width: 65px;
  display: inline-block;
  line-height: 80px;
}
.frame3-header .user-info > .btn-group .dropdown-toggle > span:last-child {
  font-size: 12px;
  padding-left: 5px;
  display: inline-block;
  position: absolute;
  top: 33px;
}
.frame-content > .menu {
  height: 100%;
  width: 60px;
  background: #2f3a4c;
}
.frame-content > .menu > nav {
  height: 100%;
}
.frame-content > .menu > nav .menu-item {
  height: 75px;
  color: white;
  text-align: center;
  padding: 10px 0;
  cursor: pointer;
  position: relative;
}
.frame-content > .menu > nav .menu-item i {
  font-size: 30px;
}
.frame-content > .menu > nav .menu-item .title {
  width: 60px;
  display: inline-block;
  text-overflow: clip;
  color: #d1d1d3;
  margin-top: 5px;
}
.frame-content > .menu > nav .menu-item .menu-arrow {
  margin: -42px 10px 0 50px;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-right: 13px solid white;
  position: absolute;
  z-index: 9999;
  display: none;
}
.frame-content > .menu > nav .menu-item:hover {
  background: #1c2431;
}
.frame-content > .menu > nav .menu-item.active {
  background: #1c2431;
}
.frame-content > .menu > nav .menu-item.active .menu-arrow {
  display: block;
}
.frame-content > .content {
  height: 100%;
  flex: 1;
}
.frame-content > .content .map-content {
  position: relative;
}
.frame-content > .content .map-content .map-content-left {
  position: absolute;
  height: 100%;
  width: 280px;
  background: white;
  z-index: 9999;
  display: none;
}
.frame-content > .content .map-content .map-content-left .map-content-left.show {
  display: block;
}
.frame-content > .content .map-content .map-content-left > div {
  height: 100%;
}
.frame-content > .content .map-content .map-content-left > div > div {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}
.frame-content > .content .map-content .map-content-right {
  height: 100%;
}
/*----------------------------
            常用页面样式
 -----------------------------*/
.under-design {
  height: 100%;
  background: #a6c3de;
  text-align: center;
  display: flex;
}
.under-design .page-content {
  margin: auto;
}
.under-design i {
  font-size: 120px;
  color: rgba(0, 0, 0, 0.2);
  align-self: center;
}
.under-design .title {
  color: #FFF;
  font-size: 60px;
  text-align: center;
}
/*----------------------------
            通用样式
 -----------------------------*/
.btn {
  border-radius: 0;
  min-width: 90px;
}
.ui-grid-top-panel {
  background: #f5f5f5 !important;
}
/*----------------------------
            插件补充样式
 -----------------------------*/
#loading-bar .bar {
  top: auto !important;
  left: auto !important;
  background-color: red;
}
#loading-bar-spinner {
  color: red;
  top: auto !important;
  left: auto !important;
}
.fr-box.fr-basic .fr-element {
  white-space: normal;
  word-break: break-all;
}
/*----------------------------
            兼容低分屏样式
 -----------------------------*/
@media screen and (min-width: 1366px) and (max-width: 1600px) {
  .query-form > header > h4 {
    padding: 10px;
  }
  .ngdialog.ngdialog-theme-default {
    padding-top: 80px;
    padding-bottom: 80px;
  }
}
.pageload-overlay {
  position: absolute;
}
.pageload-overlay::after,
.pageload-overlay::before {
  position: absolute;
}
@media screen and (max-width: 1366px) {
  .frame2 > header {
    height: 114px !important;
  }
  .frame2 > header.full-screen {
    height: 44px !important;
  }
  .header-up {
    background: #047adf url(img/head-bg.png) no-repeat 100% !important;
    height: 70px !important;
  }
  .header-up .system-info .logo img {
    width: 70px !important;
  }
  .header-up .tools .theme-option > span {
    line-height: 70px !important;
  }
  .header-up .user-info > .btn-group .user-img {
    margin-top: 5px !important;
  }
  .header-up .user-info > .btn-group .dropdown-toggle {
    line-height: 70px !important;
    height: 70px !important;
  }
  .header-up.full-screen {
    height: 0 !important;
  }
  .service-header div:nth-child(1) {
    width: 200px !important;
  }
  .service-center .service-tree {
    width: 200px !important;
  }
  .frame-content .menu .menu-item .nav {
    height: 44px !important;
    padding: 12px 0px 0px 30px !important;
  }
  .frame-content .menu .menu-item .sub-item > li {
    height: 44px !important;
    padding-top: 12px !important;
  }
  .query-form header h4 {
    padding: 10px 0px 10px 10px !important;
  }
  .query-form .query-content {
    padding: 0px 15px !important;
  }
  .query-form .query-content .tool-bar {
    height: 44px !important;
  }
  .query-form .query-content .query-condition {
    padding: 8px 0px 0px 0px!important;
  }
  .query-form .query-content .query-condition .query-input {
    width: 560px !important;
  }
  .query-form .query-content .query-condition .query-input .query-item {
    width: 280px !important;
  }
  .query-form .query-content .query-condition .query-input .query-item > div {
    width: 180px !important;
  }
  .query-form .query-content .query-condition .query-tool {
    padding-left: 40px !important;
  }
  .query-form .query-content .tool-bar .tool-tag .tag-container {
    margin: auto !important;
  }
  .query-form .query-content .tool-buttons .btn-link {
    font-size: 14px !important;
    border: none !important;
  }
  .query-form .query-content .content-table table > tbody > tr:nth-child(even) > td,
  .query-form .query-content .content-table table > tbody > tr:nth-child(even) > th {
    background-color: #f9f9f9;
  }
  .query-form .query-content .content-table .table-striped > tbody > tr:nth-child(odd) > td,
  .query-form .query-content .content-table .table-striped > tbody > tr:nth-child(odd) > th {
    background-color: #ffffff !important;
  }
  .query-form .query-content .app-content ul li > span > div > div > div.title {
    height: 25px !important;
    line-height: 25px !important;
  }
  .query-form .query-content .app-content ul li > span > div > div > div.image {
    width: 40% !important;
    padding-top: 40% !important;
  }
  .query-form .query-content .app-content ul li > span > div > div > div.image:hover {
    width: 45% !important;
    padding-top: 45% !important;
  }
  .ngdialog.ngdialog-theme-default.custom.sm .ngdialog-content {
    width: 570px !important;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content {
    width: 900px !important;
    height: 450px !important;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content .dialog-header {
    padding: 10px !important;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content .dialog-content {
    height: 300px !important;
    padding: 0px 30px 10px 30px!important;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content .nav-tabs {
    margin: 0px 10px;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content .gm-pagination .pager-loose {
    margin: 10px 0px !important;
    line-height: 1em;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content .gm-pagination .pager-loose > li > a {
    padding: 3px 9px !important;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content .gm-pagination .page-tool {
    margin: 13px 0 0 10px !important;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content .form-simple .table td.td-label {
    padding: 13px 8px 0px 8px !important;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content .form-simple .table td.td-label label {
    margin-bottom: 0px !important;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content .form-simple .table td.td-value {
    padding: 13px 50px 0px 8px !important;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content .form-simple .table td.td-value .form-control {
    height: 25px !important;
    line-height: 1 !important;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content .form-simple .table td.td-value .gm-tree-div {
    min-height: 50px !important;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content .form-simple .table td.td-value .gm-tree-div .tree-a {
    font-size: 13px !important;
  }
  .ngdialog.ngdialog-theme-default.custom .ngdialog-content .form-simple tr.catagory span {
    margin-top: 10px !important;
  }
}



